import { createSlice } from '@reduxjs/toolkit';

// Initial quests data
const questData = {
  smallTimeDealer: {
    id: 'smallTimeDealer',
    title: 'Small Time Dealer',
    description: 'Buy 5 units of weed to start your journey.',
    requirement: {
      type: 'purchase',
      drug: 'weed',
      amount: 5,
      location: 'any'
    },
    reward: {
      money: 100,
      levelUp: true,
      reputation: 5
    },
    progress: 0,
    completed: false,
    unlocked: true,
    nextQuests: ['expandingTerritory']
  },
  expandingTerritory: {
    id: 'expandingTerritory',
    title: 'Expanding Territory',
    description: 'Sell 10 units of weed in Brooklyn.',
    requirement: {
      type: 'sell',
      drug: 'weed',
      amount: 10,
      location: 'brooklyn'
    },
    reward: {
      money: 250,
      levelUp: true,
      reputation: 10
    },
    progress: 0,
    completed: false,
    unlocked: false,
    nextQuests: ['bigPlayer']
  },
  bigPlayer: {
    id: 'bigPlayer',
    title: 'Big Player',
    description: 'Sell 20 units of cocaine in Manhattan.',
    requirement: {
      type: 'sell',
      drug: 'cocaine',
      amount: 20,
      location: 'manhattan'
    },
    reward: {
      money: 1000,
      levelUp: true,
      reputation: 25
    },
    progress: 0,
    completed: false,
    unlocked: false,
    nextQuests: ['drugOverlord']
  },
  drugOverlord: {
    id: 'drugOverlord',
    title: 'Drug Overlord',
    description: 'Accumulate $10,000 in cash.',
    requirement: {
      type: 'accumulate',
      resource: 'money',
      amount: 10000
    },
    reward: {
      money: 5000,
      levelUp: true,
      reputation: 50
    },
    progress: 0,
    completed: false,
    unlocked: false,
    nextQuests: []
  }
};

const initialState = {
  quests: questData,
  activeQuestIds: ['smallTimeDealer'],
  completedQuestIds: []
};

export const questSlice = createSlice({
  name: 'quests',
  initialState,
  reducers: {
    updateQuestProgress: (state, action) => {
      const { questId, progress } = action.payload;
      
      if (state.quests[questId]) {
        state.quests[questId].progress = progress;
        
        // Check if quest is completed
        if (progress >= state.quests[questId].requirement.amount && !state.quests[questId].completed) {
          state.quests[questId].completed = true;
          state.completedQuestIds.push(questId);
          
          // Remove from active quests
          state.activeQuestIds = state.activeQuestIds.filter(id => id !== questId);
          
          // Unlock next quests
          state.quests[questId].nextQuests.forEach(nextQuestId => {
            if (state.quests[nextQuestId]) {
              state.quests[nextQuestId].unlocked = true;
              state.activeQuestIds.push(nextQuestId);
            }
          });
        }
      }
    },
    resetQuests: () => initialState,
    loadQuestState: (state, action) => {
      return { ...action.payload };
    }
  }
});

export const { 
  updateQuestProgress, 
  resetQuests,
  loadQuestState
} = questSlice.actions;

export default questSlice.reducer; 